/* Loading */

.loading {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    width: 100%;
    height: 100%;
    z-index: 1000;
}

.error-span {
    padding: 0 16px;
}

.hide {
    opacity: 0;
    visibility: collapse;
    z-index: -1;
}

.loading img {
    transform: rotate(0deg);
    animation-name: loadSpinner;
    animation-timing-function: linear;
    animation-duration: 0.6s;
    animation-iteration-count: infinite;
}

.copi-cola-btn {
    display: flex;
    height: 20px;
    align-items: center;
    margin: 24px 0;
    position: relative;
    width: 100%;
    justify-content: center;
}

.tost-copied {
    background-color: #2ba620;
    color: #fff;
    font-size: 14px;
    border-radius: 8px;
    position: absolute;
    bottom: -35px;
    visibility: collapse;
    opacity: 0;
    width: 163px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 500ms ease-in;
    top: 0;
}

.anime-toast {
    visibility: visible;
    opacity: 1;
    transition: all 500ms ease-in;
}

@keyframes loadSpinner {
    to {
        transform: rotate(0deg);
    }
    from {
        transform: rotate(359deg);
    }
}

.form-text {
    font-size: 20px;
    line-height: 26px;
}

.link {
    text-decoration: none;
