/* Listas */

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

.list-card-item {
    display: grid;
    grid-template-columns: 1fr 24px;
    grid-template-rows: repeat(2, auto);
    align-items: center;
    padding: 16px;
    margin-bottom: 16px;
    border: 1px solid var(--border-color);
    cursor: pointer;
    border-radius: 8px;
}

.list-card-item p {
    grid-column: 1 / 1;
    grid-row: 1 / 1;
    padding-bottom: 8px;
}

.list-card-item span {
    grid-column: 1 / 1;
    grid-row: 2 / 2;
    width: 256px;
}

.list-card-item .img {
    grid-column: 2 / 2;
    grid-row: 1 / span 2;
}

.input {
    outline: 0;
    border: none;
    font-family: "Bradesco Sans";
    padding: 10px;
    font-weight: 300;
}

.d-block {
    display: block;
}

.message {
    display: grid;
    grid-template-columns: 30px 1fr;
    background-color: #fff;
    border-color: 1px solid var(--border-color);
    border-radius: 8px;
    align-items: center;
    padding: 14px;
}

.alert {
    background: #ffe8ed;
